/* The switch - the box around the slider */
.switch {
  display: inline-block;
  position: relative;
  height: 34px;
  overflow: hidden;
  width: 70px;
}

/* Hide default HTML checkbox */
.switch input {
    display:none;
}

.switch-off {
    padding: 8px 0px 6px 0px;
    position: absolute;
    right: 5px;
    transition-property: all;
    transition-duration: .4s;
    -webkit-transition-duration: .4s;
}

.switch-off-active {
    right: -32px;
}

.switch-on {
    left: -25px;
    padding: 8px 0px 6px 0px;
    position: absolute;
    transition-property: all;
    transition-duration: .4s;
    -webkit-transition-duration: .4s;
}

.switch-on-active {
    left: 5px;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 600ms;
  transition: 600ms;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(36px);
  -ms-transform: translateX(36px);
  transform: translateX(36px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

